

























import { Component, Vue, Prop, Emit } from 'vue-property-decorator'
import GanIcon from '@/packages/GanIcon'
import GanLink from '@/packages/GanLink'

@Component({
  name: 'GanCard',
  components: {
    GanIcon,
    GanLink
  }
})
export default class GanCard extends Vue {
  // cancel
  @Prop({ default: '', type: String }) footerConsent!: string
  @Prop({ default: '', type: String }) footerCancel!: string
  // 
  @Prop({ default: '', type: String }) headerText!: string

  /**
   * footer
   */

  handleFooter(clickName: string) {
    switch (clickName) {
      case 'cancel': this.$emit('cancel')
        break
      case 'consent': this.$emit('consent')
        break
    }
  }

  /**
   * more
   */

  @Emit('more')
  handleMore() {
  }

  /**
   * 
   */
  get isHaveSlot() {
    return (slot: string) => this.$scopedSlots.hasOwnProperty(slot)
  }
}
